import React, {useEffect, useState} from 'react';
import classnames from 'classnames';

import './style.scss';
import '../../style.css';

import { IoMdCheckmark } from 'react-icons/io'

const BookItem = ({selected, service, persone, price, onClick, inSale, className}) => {
    const [ state, setState ] = useState(selected);
    const renderState = e => {
      onClick(e);
    };

    useEffect(() => {
      setState(selected)
    }, [selected]);
    return(
        <div className={classnames( `BookItem flexible aCenter jBetween ${className}`,  {'actBook':state }  )} onClick={renderState}>
            <div className='flexible aCenter'>
                <div className={classnames( 'itemIcon flexible aCenter jCenter',  {'activeItemIcon': state })} >
                    { state ? <IoMdCheckmark fill='ghostWhite'/> : null}
                </div>
                <span
                  className={classnames( 'itemFont flexible aCenter', className, {'activeItemFont': state })}
                >
                  {service || persone}
                  {inSale && <img src='coupon.png' style={{ width: 25, margin: 6}}/>}
                </span>
            </div>
            <p className={classnames('price', {activeBookPrice : state})}>{price}</p>
        </div>
    )
};

export default BookItem;
