import React, { useState } from 'react';
import classnames from 'classnames';
import HtmlToReact from 'html-to-react';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';

import { useStore } from '../../../store/DataProvider';

import { BookItem, LineText } from '../../../components';

import 'react-tabs/style/react-tabs.css';

const parser = new HtmlToReact.Parser();

const sales = {
  2: 13.75,
  3: 13.5,
  4: 23.8,
  5: 27.2,
  6: 29.754601227,
};

function getSale(count, subTotal) {
  if (sales[count]) {
    return Math.ceil(subTotal - subTotal * sales[count] / 100);
  } else {
    return Math.ceil(subTotal - subTotal * sales[6] / 100);
  }
}

const PricesWithCalc = ({ page, setServices }) => {
  const { state: { services, subCategories } } = useStore();
  const { prices } = page;
  const [selected, setSelected] = useState({});

  const lasers = services.filter(item => item._doc._id === '5e33197fa12d7f12cf4ac805' || item._doc._id === '5e331980a12d7f12cf4ac806')
  .map(item => ({ ...item._doc, services: [...item.services] }));

  let total = 0;

  for(let key in selected) {
    total += selected[key];
  }

  const subTotal = Object.keys(selected).length > 1 ? getSale(Object.keys(selected).length, total) : total;

  const handleSelected = (id, price) => {
    if(selected[id]) {
      const { [id]: removable, ...without } = selected;
      setSelected(without);
      setServices(without);
    } else {
      setServices({ ...selected, [id]: price });
      setSelected({ ...selected, [id]: price });
    }
  };

  const grouped = [...prices].reduce((acc, cat) => {
    if (cat.data.length) {
      acc[acc.length -1 ].data.push(cat);
    } else {
      acc.push({...cat, data: []});
    }

    return acc;
  }, []);

  return (
    <div className='shelackPrices booking-select-service'>
      <Tabs onSelect={(f, n) => f !== n && setSelected({})}>
        <TabList>
          {grouped.map(cat => (
            <Tab>
              <LineText
                heading={false}
                lineStyle="1px solid"
                headTitle={cat.title}
                textColor=' rgb(166, 129, 91)'
                flexName='flexible grow '
                style={{ marginTop: 20 }}
                width="100%"
              />
            </Tab>
          ))}
        </TabList>
        {grouped.sort((a,b) => a.order - b.order).map(cat => {
          const currentLaser = lasers.find(item => item.title.indexOf(cat.title) >= 0);
          const grouped = currentLaser.services.reduce((acc, elem) => {
            if (acc[elem.subCategoryId]) {
              acc[elem.subCategoryId].data.push(elem);
            } else {
              acc[elem.subCategoryId] = { title: subCategories.find(item => item._id === elem.subCategoryId)?.title, data: [elem] }
            }
            return acc;
          }, {});

          return (
            <TabPanel>
              {Object.values(grouped).map(item => (
                <div className='category' style={{ marginTop: 20 }}>
                  <div>
                    <p className='active-color'>{item.title}</p>
                    {item.data.map(price => (
                      <BookItem
                        selected={selected[price._id]}
                        className={classnames('selectable-row', { selected: selected[price._id] })}
                        onClick={() => handleSelected(price._id, price.price)}
                        service={price.title}
                        price={`${price.price} `}
                      />
                    ))}
                  </div>
                </div>
              ))}
            </TabPanel>
          )
        })}
      </Tabs>
      <div className='category'>
        <div>
          <p className='active-color'>
            Total
            <span style={{ float: 'right' }}>
              {subTotal !== total && <span style={{ textDecoration: 'line-through', fontSize: 16 }}>{total}  </span>}
              {subTotal} 
            </span>
          </p>
        </div>
      </div>
    </div>
  )
}
export default PricesWithCalc;
